<template>
  <div class="context">
    <div class="profile">
      <div class="left-side">
        <div class="avatar content">
          <div class="avatar-content">
            <img id="avatar" class="photo" :src="lady.avatarOriginal" alt="">
          </div>
        </div>
      </div>
      <div class="right-side">
        <div class="description content">
          <h3 class="title">{{ lady.name }} {{ lady.lastname }}</h3>
          <div><span>ID: {{ lady.id }}</span></div>
          <div><span>Login: {{ lady.email }}</span></div>
          <div><span>Последнее обновление: {{ lady.lastRefresh }}</span></div>
        </div>
        <div class="content">
          <div class="title finances">
            <h3>Начисления: {{ totalFinance }}</h3>
            <button v-on:click="toExcel()" class="btn btn-outline-secondary btn-sm text-white">в excel</button>
          </div>
          <div id="date-range">
            <i class="fa fa-calendar"></i>&nbsp;
            <span></span> <i class="fa fa-caret-down"></i>
          </div>
          <div class="table-container">
            <table id="grid-basic" class="filterable-table bootgrid-table">
              <thead>
              <tr>
                <th data-column-id="date">Дата</th>
                <th data-column-id="reason">Причина</th>
                <th data-column-id="amount" data-type="numeric">Баланс</th>
              </tr>
              </thead>
              <tbody id="grid-basic-result">
              <tr v-for="finance in finances">
                <td>{{ finance.dateTime }}</td>
                <td>{{ finance.reason }}</td>
                <td>{{ finance.amount }}</td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="gallery content">
          <h3 class="title">Альбом:</h3>
          <div class="images">
            <img v-for="photo in photos" data-fancybox="gallery" class="photo" :src="photo" alt="">
          </div>
        </div>
        <div class="description content">
          <table style="width: 100%">
            <tbody>
            <tr>
              <td class="title"> Имя:</td>
              <td>{{ lady.name }}</td>
            </tr>
            <tr class="birth-date">
              <td class="title"> Дата рождения:</td>
              <td> {{ lady.dateBirth }}</td>
            </tr>
            <tr>
              <td class="title"> Страна:</td>
              <td>{{ lady.country }}</td>
            </tr>
            <tr>
              <td class="title"> Город:</td>
              <td>{{ lady.city }}</td>
            </tr>
            <tr>
              <td class="title"> Рост:</td>
              <td>{{ lady.height }} см</td>
            </tr>
            <tr>
              <td class="title"> Вес:</td>
              <td>{{ lady.weight }} кг</td>
            </tr>
            <tr>
              <td class="title"> Тип тела:</td>
              <td>{{ lady.bodyType }}</td>
            </tr>
            <tr>
              <td class="title"> Семейное положение:</td>
              <td>{{ lady.maritalStatus }}</td>
            </tr>
            <tr>
              <td class="title"> Образование:</td>
              <td>{{ lady.education }}</td>
            </tr>
            <tr>
              <td class="title"> Религия:</td>
              <td>{{ lady.religion }}</td>
            </tr>
            <tr>
              <td class="title"> Сигареты:</td>
              <td>{{ lady.smoking }}</td>
            </tr>
            <tr>
              <td class="title"> Алкоголь:</td>
              <td>{{ lady.drinking }}</td>
            </tr>
            <tr>
              <td class="title"> Профессия:</td>
              <td>{{ lady.occupation }}</td>
            </tr>
            <tr>
              <td class="title"> Количество детей:</td>
              <td>{{ lady.countChildren }}</td>
            </tr>
            <tr>
              <td class="title"> Цвет глаз:</td>
              <td>{{ lady.colorEye }}</td>
            </tr>
            <tr>
              <td class="title"> Цвет волос:</td>
              <td>{{ lady.colorHair }}</td>
            </tr>
            <tr>
              <td class="title"> В мужчине ищет:</td>
              <td>{{ lady.lookingFor }}
              </td>
            </tr>
            <tr>
              <td class="title"> Уровень английского:</td>
              <td>{{ lady.engLevel }}</td>
            </tr>
            <tr>
              <td class="title"> Другие языки:</td>
              <td>{{ lady.otherLangs }}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import '@/external/jquery.dataTables.min.css'
import '@/external/jquery.dataTables.min.js'
import "@fancyapps/ui/dist/fancybox.css";
import "@fancyapps/ui";
import Connection from "@/rsocket/connection";
import notification from "@/notify/notification";
import router from "@/views/router/routes";
import moment from "moment";
import "@/external/daterangepicker.min.js";
import "@/external/daterangepicker.css"
import Swal from "sweetalert2";

export default {
  name: "TalkyTimeLady",
  mounted() {
    this.id = this.$route.params.id;
    Connection.talkyTimeLady(this.id, (res) => {
      this.lady = JSON.parse(res.data.toString());
      this.photos = JSON.parse(atob(this.lady.photos));
    }, (error) => {
      notification("error", error.prettyText)
      router.push("/dashboard/talkytime/ladies");
    });
    let fromDate = moment(sessionStorage.getItem("ladyFinanceFrom") || moment().subtract(29, 'days').toJSON());
    let toDate = moment(sessionStorage.getItem("ladyFinanceTo") || moment().toJSON());

    $('#date-range').daterangepicker({
      startDate: fromDate,
      endDate: toDate,
      ranges: {
        'Сегодня': [moment(), moment()],
        'Вчера': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
        'Последние 7 дней': [moment().subtract(6, 'days'), moment()],
        'Последние 30 дней': [moment().subtract(29, 'days'), moment()],
        'Текущий месяц': [moment().startOf('month'), moment().endOf('month')],
        'Прошлый месяц': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
      }
    }, this.updateFromTo);

    this.updateFromTo(fromDate, toDate);
  },
  methods: {
    toExcel() {
      let mapNight = new Map();
      let mapMorning = new Map();
      let mapEvening = new Map();

      this.finances.forEach(element => {
        let [date, time] = element.dateTime.split(" ");
        if (this.isTimeBetween(time, "20:00:00", "23:59:59") || this.isTimeBetween(time, "00:00:00", "03:59:59")) {
          mapNight.set(date, (Number(mapNight.get(date) || 0) + Number(element.amount)).toFixed(2));
        } else if (this.isTimeBetween(time, "04:00:00", "11:59:59")) {
          mapMorning.set(date, (Number(mapMorning.get(date) || 0) + Number(element.amount)).toFixed(2));
        } else if (this.isTimeBetween(time, "12:00:00", "19:59:59")) {
          mapEvening.set(date, (Number(mapEvening.get(date) || 0) + Number(element.amount)).toFixed(2));
        }
      });

      let night = Array.from(mapNight.values()).join("\t");
      let morning = Array.from(mapMorning.values()).join("\t");
      let evening = Array.from(mapEvening.values()).join("\t");
      Swal.fire({
        title: 'Статистика по сменам',
        html:
            '<h3>ночь: </h3><input id="statistic_night" class="swal2-input">' +
            '<h3>утро: </h3><input id="statistic_morning" class="swal2-input">' +
            '<h3>вечер: </h3><input id="statistic_day" class="swal2-input">',
        focusConfirm: false
      });
      $("#statistic_night").val((night + "").replace(/\./g, ","));
      $("#statistic_morning").val((morning + "").replace(/\./g, ","));
      $("#statistic_day").val((evening + "").replace(/\./g, ","));
    },
    getSeconds(timeString) {
      let [hh, mm, ss] = timeString.split(":");
      return ((parseInt(hh) * 60) * 60) + (parseInt(mm) * 60) + parseInt(ss);
    },
    isTimeBetween(checkTime, startTime, endTime) {
      checkTime = this.getSeconds(checkTime);
      return (checkTime >= this.getSeconds(startTime) && checkTime <= this.getSeconds(endTime));
    },
    getFinances(fromDateTime, toDateTime) {
      Connection.talkyTimeFinanceList(this.id, fromDateTime, toDateTime, null, (res) => {
        let data = JSON.parse(res.data.toString());
        this.finances = data.finances;
        this.totalFinance = this.finances.reduce((acc, cur) => acc + Number(cur.amount), 0).toFixed(2);
      }).then(() => {
        $('#grid-basic').DataTable();
      });
    },
    updateFromTo(start, end) {
      let fromDateTime = start.format('YYYY-MM-DD 00:00:00');
      let toDateTime = end.format('YYYY-MM-DD 23:59:59');
      $('#date-range span').html(start.format('DD.MM.YYYY') + ' - ' + end.format('DD.MM.YYYY'));
      sessionStorage.setItem("ladyFinanceFrom", start.toJSON());
      sessionStorage.setItem("ladyFinanceTo", end.toJSON());
      this.getFinances(fromDateTime, toDateTime);
      this.startDateTime = fromDateTime;
      this.toDateTime = toDateTime;
    }
  },
  data() {
    return {
      lady: [],
      photos: [],
      finances: [],
      totalFinance: 0,
      startDateTime: "2000-01-01 00:00:00",
      toDateTime: "2000-01-01 23:59:59",
    }
  }
}
</script>

<style>
.daterangepicker {
  color: white;
}
</style>

<style lang="scss" scoped>
.context {
  display: flex;
  width: 100%;
}

#date-range {
  cursor: pointer;
  padding: 5px 10px;
  text-align: center;
  background: #00000055;
  color: white !important;
}

.profile {
  color: white;
  width: 100%;
  display: inline-flex;
  padding: 10px;
  box-sizing: border-box;

  .left-side {
    float: left;
    width: 33%;

    .change-password {
      margin-top: 10px;

      button {
        width: 100%;
      }
    }
  }

  .avatar {
    position: sticky;
    top: 10px;

    img {
      cursor: pointer;
      height: auto;
      width: 100%;
      border-radius: 6px;
      top: 0;
      position: relative;
      margin-bottom: -4px;
    }
  }

  .description {
    text-align: center;
  }

  .content {
    box-sizing: border-box;
    background: #222427;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
  }

  .avatar-content {
    position: relative;
    border-radius: 8px;
    overflow: hidden;
  }

  .right-side {
    margin-left: 10px;
    width: calc(67% - 10px);
    height: fit-content;

    .content {
      padding: 20px;
    }

    .gallery {
      .images {
        display: inline-flex;
        width: 100%;
        overflow-x: scroll;

        img {
          cursor: pointer;
          height: 200px;
          width: auto;
          margin: 5px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .profile {
    display: block;
  }

  .profile .left-side {
    float: none;
    width: 100%;
  }

  .profile .right-side {
    margin-left: 0;
    margin-top: 10px;
    width: 100%;
    height: fit-content;
  }
}

.finances {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-container {
  background: #2c2f33;
  color: #fff;
}

.filterable-table {
  overflow: hidden;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  text-align: center;
  background-color: #494e55;
  font-weight: bold;

  tbody tr {
    border-bottom: 2px solid #0000006c;
  }
}

@media screen and (max-width: 768px) {
  table.filterable-table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }

  table.filterable-table tr {
    border-bottom: 1px solid #ddd;
    display: block;
    margin-bottom: 30px;
  }

  table.filterable-table td {
    display: block;
    text-align: right;
  }

  table.filterable-table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }

  table.filterable-table td:last-child {
    border-bottom: 0;
  }
}

</style>